@font-face {
  font-family: "FontBG";
  src: url("../fontbg.ttf");
}

:root {
  --wdth: 100;
  --wght: 10;
}

body {
  font-family: "FontBG";
  font-variation-settings: "wdth" var(--wdth), "wght" var(--wght);
}

#app {
  text-align: center;
  height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 4rem;
}
